.paginator {
    min-height: 140px;
    // margin: 4em 0;
    text-align: center;
}

.paginator .next,
.paginator .prev {
    display: inline-block;
    margin: 0 4px;
    padding: 4px 12px;
    border-radius: 4px;
    border-bottom: 4px solid #3aa373;
    font-size: 14px;
    color: #fff;
    background-color: #4fc08d;
}

.paginator .next:hover,
.paginator .prev:hover {
    background-color: #22bd77;
}